import React from 'react';
import PaymentScreen from '../PaymentScreen';
import {render, fireEvent, waitFor} from '@testing-library/react-native';
import '@testing-library/jest-native/extend-expect';
import {NativeStackNavigationProp} from '@react-navigation/native-stack';
import {useNavigation} from '@react-navigation/native';
jest.mock('@react-navigation/native', () => {
  return {
    useNavigation: jest.fn().mockReturnValue({
      navigate: jest.fn(),
    }),
  };
});
const navigation = useNavigation<NativeStackNavigationProp<any>>();
describe('Payment Screen/Deregister Parking', () => {
  it('Shows time spent in parking', async () => {
    jest.useFakeTimers();
    const details = {
      reg: 'ABC123',
      time: new Date().toLocaleTimeString(),
    };
    const {getByTestId} = render(<PaymentScreen route={{params: {details}}} />);
    jest.advanceTimersByTime(2 * 60 * 60 * 1000);
    await waitFor(() =>
      expect(getByTestId('deregester-time-spent').props.children).toBe(
        'Time Spent: 2 hours 0 minutes',
      ),
    );
  });
});
